
.box {}

.box-graphic {
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.box-graphic-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(#000, 0.7);
  opacity: 0.1;
  transition: 0.5s all;
  z-index: 2;
}

.box-graphic:hover .box-graphic-overlay{
  opacity:1;
}


.box-graphic-overlay:before,
.box-graphic-overlay:after {
  content: '';
  width: 70px;
  height: 1px;
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.box-graphic-overlay:after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.box-graphic img {
  display: block;
  height: auto;
  max-width: 100%;
  transiton: 4s;

}

.box-title a{
  font-size: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: #222;
}

.box-text {
  font-size: 17px;
  color: #999;
  margin-bottom: 30px;
}

