*, *:after, *:before {
  box-sizing: border-box;
}

body {
  margin:0;
  font-family: "Nunito Sans", sans-serif;
}

.container {
  max-width: 1350px;
  margin: 0 auto;
}

.section-header {
  text-align: center;
}

.section-header-text {
  font-size: 20px;
}

.section-header-title {
  font-size: 40px;
  text-transform: uppercase;
  padding-bottom: 20px;
  position: relative;
  margin-top: 10px;
}

.section-header-title:after {
  content: '';
  height: 2px;
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 125px;
  transform: translate(-50%, 0);
  background-color: $color-main;
}

.row {
  display: flex;
}

.row .col {
  margin: 0 15px;
  flex-grow: 1;
}

.button {
  font-size: 15px;
  text-transform: uppercase;
  color: #fff;
  display: inline-block;
  padding: 18px 55px;
  background: #222;
  border-radius: 30px;
  text-decoration: none;
  font-weight: bold;
  transition: 0.5s all;
}
.button:hover {
  background: $color-main;
  color: #222;
}